<template>
<div class='login'>
    <el-row class="top">
        <div class="content">
            <img src="@/assets/images/logo.png" alt="">
        </div>
    </el-row>
    <el-row class="contain">
        <div class="content">
            <img src="https://s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg" alt="">
            <div class="loginBox">
                <div class="warning" v-if='warning'>
                    <i class='el-icon-remove'></i>
                    <span>{{warning}}</span>
                </div>
                <p>
                    <span></span>
                    <span class='loginByPhone'>
                        <i class='el-icon-mobile-phone'></i>
                    </span>
                </p>
                <el-input
                    placeholder="//"
                    prefix-icon="el-icon-search"
                    v-model="userId">
                </el-input>
                <el-input
                    placeholder=""
                    prefix-icon="el-icon-search"
                    type="password"
                    v-model="userPassword">
                </el-input>
                <router-link tag='span' to="/w">?</router-link>
                <el-button type="primary" @click='loginClick'></el-button>
                <p class="register">?
                    <router-link tag="span" :to="{name: 'register'}"></router-link>
                </p>
                <div class="friendLink">
                    <p></p>
                    <a href="#" class='qq'></a>
                    <a href="#" class='wb'></a>
                </div>
            </div>
        </div>
    </el-row>
    <el-row class="bottom1">
        <div class="content">
            <a href="#" v-for="(item, index) in bottomList" :key="index">{{item}}</a>
        </div>
    </el-row>
    <el-row class="bottom2">
        <div class="content">
            2019  meituan.com ICP070791 11010502025545
        </div>
    </el-row>
</div>
</template>

<script>
import axios from '@/api/index.js'
export default {
    data() {
        return {
            userId: '',
            userPassword: '',
            warning: '',
            bottomList: ['', '', '', '', '']
        }
    },
    methods: {
        loginClick() {
            if (!this.userId && !this.userPassword) {
                this.warning = '';
            } else if (!this.userId) {
                this.warning = '';
            } else if (!this.userPassword) {
                this.warning = '';
            }
            if (!this.warning) {
                axios.login({
                    params: {
                        userName: this.userId,
                        password: this.userPassword,
                    }
                }).then((res) => {
                    if (res.data.status == 'success') {
                        this.$router.push({name: 'myDefault'})
                        this.$store.commit('changeUserName', this.userId)
                    }
                })
                //
            }
        }
    }
}
</script>

<style lang='scss'>
    @import '@/assets/css/blank/login.scss'
</style>


