<template>
<div class='register'>
    <el-row class='topBar'>
        <div class="content">
            <img src="@/assets/images/logo.png" alt="">
            <div>
                <p>?</p>
                <router-link tag="button" :to='{name: "login"}'></router-link>
            </div>
        </div>
    </el-row>
    <el-row class='contain'>
        <div class="content">
            <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
                <el-form-item label="" class="name">
                    <el-input v-model="formLabelAlign.name" @blur="nameBlur"></el-input>
                    <span>,</span>
                    <div class="passwordWarning warning" v-if='formLabelAlign.nameWarning'>
                        <i class='el-icon-remove'></i>
                        <span>{{formLabelAlign.nameWarning}}</span>
                    </div>
                    <!-- <div class="success" v-else>
                        <i class='el-icon-success'></i>
                    </div> -->
                </el-form-item>
                <div class="btn">
                    
                </div>
                <el-form-item label="">
                    <el-input v-model="formLabelAlign.yzm"></el-input>
                </el-form-item>
                <el-form-item label="" class='password' >
                    <el-input v-model="formLabelAlign.password" type="password" @input="passwordInput"></el-input>
                    <div class="passwordWarning warning" v-if='formLabelAlign.passwordWarning'>
                        <i class='el-icon-remove'></i>
                        <span>{{formLabelAlign.passwordWarning}}</span>
                    </div>
                    <!-- <div class="success" v-else>
                        <i class='el-icon-success'></i>
                    </div> -->
                </el-form-item>
                <div class="bar">
                    <div :class="['barContent', formLabelAlign.passwordLevel]">
                    </div>
                    <div class="line">
                        <div class="line1"></div>
                        <div class="line2"></div>
                        <div class="line3"></div>
                    </div>
                </div>
                <el-form-item label="">
                    <el-input v-model="formLabelAlign.rePassword" type="password" @input="rePasswordInput"></el-input>
                    <div class="passwordWarning warning" v-if='formLabelAlign.rePasswordWarning'>
                        <i class='el-icon-remove'></i>
                        <span>{{formLabelAlign.rePasswordWarning}}</span>
                    </div>
                    <!-- <div class="success" v-else>
                        <i class='el-icon-success'></i>
                    </div> -->
                </el-form-item>
                <div class="registerBtn" @click="register">
                    
                </div>
                <br>
                <a href="#"></a>
            </el-form>
        </div>
    </el-row>
    <el-row class='footer'>
        <div class="content">
            meituan.com  ICP070791  11010502025545
        </div>
    </el-row>
</div>
</template>

<script>
import axios from "@/api/index.js"
export default {
data() {
    return {
        labelPosition: 'right',
        formLabelAlign: {
          name: '',
          yzm: '',
          password: '',
          rePassword: '',
          passwordLevel: '', // weak middle strong
          nameWarning: '',
          passwordWarning: '',
          rePasswordWarning: '',

        }
    }
},
methods: {
        nameBlur() {
            if (this.formLabelAlign.name.length < 4) {
                this.formLabelAlign.nameWarning = '6';
            } else if (this.formLabelAlign.name.length > 16) {
                this.formLabelAlign.nameWarning = '16';
            }
        },
        passwordInput() {
            if (this.formLabelAlign.password.length < 6) {
                this.formLabelAlign.passwordWarning = '6';
                this.formLabelAlign.passwordLevel = '';
            } else if (this.formLabelAlign.password.length < 8) {
                this.formLabelAlign.passwordWarning = '';
                this.formLabelAlign.passwordLevel = 'weak';
            } else if (this.formLabelAlign.password.length < 12) {
                this.formLabelAlign.passwordWarning = '';
                this.formLabelAlign.passwordLevel = 'middle';
            } else if (this.formLabelAlign.password.length < 16) {
                this.formLabelAlign.passwordWarning = '';
                this.formLabelAlign.passwordLevel = 'strong';
            } else {
                this.formLabelAlign.passwordWarning = '16';
                this.formLabelAlign.passwordLevel = '';
            }
        },
        rePasswordInput() {
            if (this.formLabelAlign.password != this.formLabelAlign.rePassword ) {
                this.formLabelAlign.rePasswordWarning = '';
            } else {
                this.formLabelAlign.rePasswordWarning = '';
            }
        },
        register() {
            axios.register({
                params: {
                    userName: this.formLabelAlign.name,
                    password: this.formLabelAlign.password,
                    rePassword: this.formLabelAlign.rePassword,
                }
            }).then(res => {
                if (res.data.status == 'success') {
                    this.$router.push({name: 'myDefault'});
                    this.$store.commit('changeUserName', this.formLabelAlign.userName);
                } else if (res.data.msg == '') {
                    this.formLabelAlign.nameWarning = '';
                }
            })
        }
    }
}
</script>

<style lang='scss'>
    @import '@/assets/css/blank/register.scss'
</style>


