<template>
<div class='allCity'>
    <letter-list :title='letterTitle' :cityList='letter' />
    <dl v-for="(item, index) in letterList" :key="index" :id="'city-' + item.letter">
        <dt>{{item.letter}}</dt>
        <dd v-for="(v, i) in item.cityList" :key="item.letter + v + i" @click='cityClick(v)'>{{v}}</dd>
    </dl>
</div>
</template>

<script>
import letterList from '@/components/changeCity/hotCity.vue'
export default {
    data() {
        return {
            letter: 'ABCDEFGHJKLMNPQRSTWXYZ'.split(''),
            letterList: [
                {
                    letter: 'A',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'B',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'C',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'D',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'E',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'F',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'G',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'H',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'J',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'K',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'L',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'M',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'N',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'P',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'Q',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'R',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'S',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'T',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'W',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'X',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'Y',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
                {
                    letter: 'Z',
                    cityList: ["","","","","","","","","","","","","","","","","","","","","","","","","",""]
                },
            ],
            letterTitle: '',
        }
    },
    components: {
        letterList
    },
    methods: {
        cityClick(v) {
            this.$store.commit('setPosition', v);
            this.$router.push({name: 'myDefault'})
        }
    }
}
</script>

<style lang='scss'>
    @import '@/assets/css/changeCity/allCity.scss'
</style>




