<template>
    <div class='topBar'>
        <div class="contain">
            <div class="position">
                <i class='el-icon-location'></i>
                <div class="cityName">{{position}}</div>
                <router-link tag="div" :to="{name:'changeCity'}" class="changeCity"></router-link>
                <div class="relatedBox">[
                    <div class="related"></div>
                    <div class="related"></div>
                    <div class="related"></div>
                    ]
                </div>
                <div class='loginStatus'>
                    <div class='noLogin' v-if="!userName">
                        <router-link tag="div" :to="{name: 'login'}" class="login"></router-link>
                        <router-link tag="div" :to="{name: 'register'}" class="register"></router-link>
                    </div>
                    <div class='logined' v-else>
                        <router-link tag='div' :to="{name: 'userCenter'}" class='userName'>
                            {{userName}}
                        </router-link>
                        <div class="quit" @click='quit'>
                            
                        </div>
                    </div>
                </div>
            </div>
            <div class="nav">
                <div class="myMt list">
                    <router-link tag="span" class="nav-tag" to='/'></router-link>
                    <ul class="show">
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                </div>
                <div class="app">
                    <router-link tag="span" class="nav-tag" to='/'>APP</router-link>
                </div>
                <div class="centre list">
                    <router-link tag="span" class="nav-tag" to='/'></router-link>
                    <ul class="show">
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                </div>
                <div class="map list">
                    <router-link tag="span" class="nav-tag" to='/'></router-link>
                    <div class="show">
                        <ul>
                            <li class="map-item">
                                <ul class='hotal'>
                                    <h4></h4>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                </ul>
                            </li>
                            <li class="map-item">
                                <ul class='food'>
                                    <h4></h4>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                </ul>
                            </li>
                            <li class="map-item">
                                <ul class='movie'>
                                    <h4></h4>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                </ul>
                            </li>
                            <li class="map-item">
                                <ul class='map-app'>
                                    <h4></h4>
                                    <li>
                                        <a href="">
                                            <img src="@/assets/images/meituan.png" alt="">
                                        </a>
                                    </li>
                                    <li>
                                        <a href="">
                                            <img src="@/assets/images/waimai.png" alt="">
                                        </a>
                                    </li>
                                    <li>
                                        <a href="">
                                            <img src="@/assets/images/zhenguo.png" alt="">
                                        </a>
                                    </li>
                                    <li>
                                        <a href="">
                                            <img src="@/assets/images/dianping.png" alt="">
                                        </a>
                                    </li>
                                    <li>
                                        <a href="">
                                            <img src="@/assets/images/maoyan.png" alt="">
                                        </a>
                                    </li>
                                </ul>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    computed: {
        userName() {
            return this.$store.state.userName
        },
        position() {
            return this.$store.state.position
        }
    },
    methods: {
        quit() {
            this.$store.commit('changeUserName', '')
        }
    }
}
</script>

<style scoped lang='sass'>
@import '@/assets/css/header/topBar.scss'
</style>



