<template>
<div class='commonList'>
    <ul class='nav'>
        <li :class="{'active': active == 'default'}" @click="navClick('default')"></li>
        <li :class="{'active': active == 'price'}" @click="navClick('price')">
            <p v-if="priceState == 'default'"></p>
            <p v-if="priceState == 'down'"></p>
            <p v-if="priceState == 'up'"></p>
            <div :class="{'up': true, 'active': priceState == 'up'}"></div>
            <div :class="{'down': true, 'active': priceState == 'down'}"></div>
        </li>
        <li :class="{'active': active == 'popularity'}" @click="navClick('popularity')"></li>
        <li :class="{'active': active == 'evaluation'}" @click="navClick('evaluation')"></li>
    </ul>
    <ul class='list'>
        <li v-for="(item, index) in list.data" :key="index">
            <router-link tag="img" to="/xiangqing" :src="item.image" alt="" />
            <router-link tag="h2" to="/xiangqing" class="title">{{item.title}}</router-link>
            <p>
                <el-rate 
                v-model="item.score"
                disabled
                text-color="#ff9900">
                </el-rate>
                <span class='evaluate' v-if="item.score > 4.5"></span>
                <span class='evaluate' v-else-if="item.score > 4"></span>
                <span class='evaluate' v-else-if="item.score > 3"></span>
                <span class='score'>{{item.score}}</span>
                <span class='evaluateNum'>{{item.commentNum}}</span>
            </p>
            <p class="info">
                <span class="position">{{item.address}}</span>
                <a href="#">
                    <i class='el-icon-location'></i>
                    <span></span>
                </a>
            </p>
            <p class='avgPrice'>{{item.avgPrice}}</p>
            <ul class="discounts">
                <li v-for="(v, i) in item.hidden ? item.dealItems.slice(0,2) : item.dealItems" :key="i">
                    <router-link tag="div" to="/xiangqing" class="discountsTitle">{{v.title}}</router-link>
                    <p class='priceList'>
                        <span class='price'>{{v.price}}</span>
                        <span class='counterPrice'>{{v.counterPrice}}</span>
                        <span class='saleNum'>{{v.saleNum}}</span>
                    </p>
                </li>
                <span class='showMore' v-if="item.dealItems.length > 2 && item.hidden == true" @click="showMoreClick(index)">
                    {{item.dealItems.length - 2}}
                    <i class="el-icon-arrow-down"></i>
                </span>
                <span class='showMore' v-if="item.dealItems.length > 2 && item.hidden == false" @click="showMoreClick(index)">
                    
                    <i class="el-icon-arrow-up"></i>
                </span>
            </ul>
        </li>
    </ul>
</div>
</template>

<script>
// import axios from 'axios';
export default {
    data() {
        return {
            hidden: [],
            active: 'default',
            priceState: 'default',
            list: {
                "status": "success",
                "msg": "",
                "data": [{
                    "image": "https://p1.meituan.net/deal/3647384a2feabb144547f40e74e8bf56143212.jpg@220w_125h_1e_1c",
                    "title": "",
                    "type": "food",
                    "score": 4.1,
                    "commentNum": 23,
                    hidden: true,
                    "comment": [{
                        "username": "xxxx",
                        "evalaute": ""
                    }],
                    "tab": ["", ""],
                    "address": "3.5",
                    "avgPrice": 64,
                    "dealItems": [{
                        "title": "10-14",
                        "price": 909,
                        "counterPrice": 1150,
                        "saleNum": 0,
                        "priceType": ""
                    }]
                },{
                    "image": "https://p1.meituan.net/deal/3647384a2feabb144547f40e74e8bf56143212.jpg@220w_125h_1e_1c",
                    "title": "",
                    "type": "food",
                    "score": 4.1,
                    "commentNum": 45,
                    hidden: true,
                    "comment": [{
                        "username": "xxxx",
                        "evalaute": ""
                    }],
                    "tab": ["", ""],
                    "address": "35",
                    "avgPrice": 64,
                    "dealItems": [{
                        "title": "10-14",
                        "price": 909,
                        "counterPrice": 1150,
                        "saleNum": 0,
                        "priceType": ""
                    },{
                        "title": "10-14",
                        "price": 909,
                        "counterPrice": 1150,
                        "saleNum": 0,
                        "priceType": ""
                    },{
                        "title": "10-14",
                        "price": 909,
                        "counterPrice": 1150,
                        "saleNum": 0,
                        "priceType": ""
                    }]
                }]
            }
        }
    },
    // created() {
    //     this.list.data.forEach((item) => {
    //         item.hidden =
    //     })
    // },
    methods: {
        navClick(val) {
            if (this.priceState != 'default' && val == 'price') {
                this.priceState = this.priceState == 'up' ? 'down' : 'up';
            }else {
                this.active = val;
                if (val == 'price') {
                    this.priceState = 'up'
                } else {
                    this.priceState = 'default'
                }
            }
        },
        showMoreClick(index) {
            this.list.data[index].hidden = !this.list.data[index].hidden
        }
    }
}
</script>

<style lang='scss'>
    @import '@/assets/css/goodsList/commonList.scss'
</style>


