<template>
<div class='mainPage'>
    <my-menu />
    <life />
    <container v-for="(item, index) in containerList" :key="index" :topList='containerList[index]' />
    <m-nav />
</div>
</template>

<script>
import myMenu from '@/components/page/menu.vue'
import life from '@/components/page/life.vue'
import container from '@/components/page/container.vue'
import mNav from '@/components/page/nav.vue'
export default {
    data() {
        return {
            containerList: [
                {
                    title: '',
                    class: 'iStyle',
                    children: ['', '', 'SPA', '', '']
                },
                {
                    title: '',
                    class: 'movie',
                    children: ['', '']
                },
                {
                    title: '',
                    class: 'hotal',
                    children: ['', '', '', '', '', '', '', '', '', '']
                },
                {
                    title: '',
                    class: 'like',
                    children: ['']
                },
            ]
        }
    },
    components: {
        myMenu,
        life,
        container,
        mNav: mNav
    }
}
</script>

<style scope>

</style>


